let THREE = window.THREE



// : threejs camera,

class LMCamera {
  constructor({fieldOfView = 55, nearPlane = 1, farPlane = 10000, canvasWidth, canvasHeight} = {}) {
    let aspectRatio = canvasWidth / canvasHeight
    this.camera = new THREE.PerspectiveCamera(fieldOfView, aspectRatio, nearPlane, farPlane)
    this.camera.position.set (0, 0, 300);
    this.debug = new THREE.CameraHelper(this.camera);
    this.debug.name = 'shadowLightDebug'
  }
}
export default LMCamera



