let THREE = window.THREE

import LMCamera from './LMCamera'
import LMRender from './LMRender'
import LMAudio from './LMAudio'
import LMLight from './LMLight'


let that = null

/**
 * threejs ,.
 */
class LMThree {
  constructor({canvasHeight=window.innerHeight, canvasWidth=window.innerWidth, canvasFatherId}={}) {
    window.THREE = THREE
    that = this
    // 
    this.scene = null
    // 
    this.lmcamera = null
    // 
    this.lmrender = null
    // 
    this.lmaudio = null
    // 
    this.lmlight = null
    this.controls = null

    this.initScene()
    this.initCamera({canvasHeight, canvasWidth})
    this.initRender({canvasHeight, canvasWidth, canvasFatherId})
    this.initAudio()
    this.initLight()
    this.initCtrols()
    window.lmthree = this
    console.log(this)
  }

  initCamera({canvasHeight, canvasWidth}) {
    this.lmcamera = new LMCamera({canvasHeight, canvasWidth})
    this.scene.add(this.lmcamera.camera)
  }

  initScene() {
    this.scene = new THREE.Scene()
    // this.scene.background = new THREE.Color( 0x0a0026 );
    this.scene.fog = new THREE.Fog(0x000000, 150, 500);
    // 
    window.scene = this.scene
  }

  initRender({canvasHeight, canvasWidth, canvasFatherId}={}) {
    this.lmrender = new LMRender({canvasHeight, canvasWidth, canvasFatherId})
  }

  initAudio() {
    this.lmaudio = new LMAudio()
  }

  initLight() {
    this.lmlight = new LMLight()
    this.scene.add(this.lmlight.hemisphereLight)
    this.scene.add(this.lmlight.shadowLight)
    this.scene.add(this.lmlight.backLight)
  }

  initCtrols() {
    this.controls = new THREE.OrbitControls(this.lmcamera.camera, this.lmrender.render.domElement)
    this.controls.autoRotate = false;
    this.controls.enabled = true;
    this.controls.minDistance = 50;
    this.controls.maxDistance = 300;
    this.controls.minZoom = 0;
    this.controls.maxZoom = 500;
  }
  
}
export default LMThree



