// https://dvcs.w3.org/hg/audio/raw-file/tip/webaudio/specification.html#OfflineAudioContext-section

class LMAudio {
  constructor() {
      this.state = {
        isReadyPlay: false,
        process: 0,
        playing: false
      }

      window.AudioContext = window.AudioContext || window.webkitAudioContext;

      if (!window.AudioContext) {
          alert('Your browser does not support Web Audio API');
      } else {
          this.context = new AudioContext();
          this.sourceNode = this.context.createBufferSource();
          this.sourceNode.loop = true
          this.analyser = this.context.createAnalyser();
      }
  }

  loadAudio(filePath) {
      this._setupAudioNodes();
      var request = new XMLHttpRequest();
      request.open('GET', filePath, true);
      request.responseType = 'arraybuffer';
      request.send();
      return new Promise((resolve, reject) => {
          request.onload = () => {
              this.context.decodeAudioData(request.response, buffer => {
                  this.sourceNode.buffer = buffer;
              });
              resolve();
          };
          // 
          request.addEventListener( 'progress',(event) => {
              this.state.process = Number.parseInt(event.loaded / event.total * 100)
              if (this.state.process === 100) {
                this.state.isReadyPlay = true
              }
          })
      });
  }

  play() {
      if (!this.playing) {
          this.sourceNode.start(0);
          this.playing = true
      } else {
          this.playing = false
          alert('Audio is not ready');
      }
  }

  _setupAudioNodes() {
      this.analyser.smoothingTimeConstant = 0.3;
      this.analyser.fftSize = 2048;
      this.sourceNode.connect(this.analyser);
      this.sourceNode.connect(this.context.destination);
  }

  getFrequencyData() {
      let freqData = new Uint8Array(this.analyser.frequencyBinCount);
      this.analyser.getByteFrequencyData(freqData);
      return freqData
  }

  getAverageFrequency() {
    let value = 0
    let data = this.getFrequencyData();
    for ( var i = 0; i < data.length; i ++ ) {
      value += data[ i ];
    }
    return value / data.length;
  }
}
export default LMAudio



