// 
class JetItem {
  constructor() {
    var geom = new THREE.BoxBufferGeometry(4, 4, 4);
    this.Mat = new THREE.MeshLambertMaterial({color:0xffba00, flatShading:true, transparent: true, opacity: 0.5});
    this.mesh = new THREE.Mesh(geom, this.Mat);
    this.mesh.position.set(0,-18,-25);
  }
}

class JetStream {
  constructor() {
    this.ParticleArray = [];
    this.mesh = new THREE.Object3D();
    this.intensity = 2;
    this.pointLightL = new THREE.PointLight( 0xffba00, this.intensity, 70, 2 );
    this.pointLightL.position.set( 10, -30, 0 );
    
    this.pointLightR = new THREE.PointLight( 0xffba00, this.intensity, 70, 2 );
    this.pointLightR.position.set( -10, -30, 0 );
    
    this.mesh.add(this.pointLightR,this.pointLightL);
      
    this.mesh.rotation.x = Math.PI/30
    this.nParticles = 25;  
    // Create the Cakes
    for(var i=0; i<this.nParticles; i++){
    
      this.p = new JetItem();   
      this.p.mesh.position.y = 0+Math.random()*60;   
      this.p.mesh.position.x = 0+Math.random()*8;   
      this.p.mesh.position.z = 0+Math.random()*8;
      this.p.mesh.rotation.x = 0-Math.random()*3;
      this.p.mesh.rotation.y = 0-Math.random()*3;  
      this.p.mesh.rotation.z = 0-Math.random()*3; 
      var s = 0.1+Math.random()*1.0;
      
      this.p.mesh.scale.set(s,s,s);
  
      this.mesh.add(this.p.mesh);
      this.ParticleArray.push(this.p);
    }  
  }

  jet(){
    this.mesh.position.y = Math.sin(Date.now() * 0.001) * 15 ;
    for (var i = 0; i <this.ParticleArray.length; i++){       
      this.ParticleArray[i].mesh.position.y -=.8;
      this.ParticleArray[i].mesh.scale.x += 0.05;
      this.ParticleArray[i].mesh.scale.y += 0.05;
      this.ParticleArray[i].mesh.scale.z += 0.05;
      this.ParticleArray[i].mesh.rotation.x +=0.05;
      this.ParticleArray[i].mesh.rotation.y +=0.05;
      this.ParticleArray[i].Mat.color.setRGB(1,0.7,0);
      
      if (this.ParticleArray[i].mesh.position.y <= -20 ) {
        this.ParticleArray[i].Mat.color.setRGB(1,0.5,0);
      }    
      if (this.ParticleArray[i].mesh.position.y <= -45 ) {
        this.ParticleArray[i].Mat.color.setRGB(1,0.15,0);
        this.ParticleArray[i].mesh.scale.x += 0.05;
        this.ParticleArray[i].mesh.scale.y += 0.05;
        this.ParticleArray[i].mesh.scale.z += 0.05;
      }    
      
      if (this.ParticleArray[i].mesh.position.y <= -65 ) {
        this.ParticleArray[i].Mat.color.setRGB(0.15,0.15,0.15);
        this.ParticleArray[i].mesh.scale.x += 0.05;
        this.ParticleArray[i].mesh.scale.y += 0.05;
        this.ParticleArray[i].mesh.scale.z += 0.05;
      }
      
      if (this.ParticleArray[i].mesh.position.y <= -75 ) {
        this.ParticleArray[i].mesh.position.y=-12;   
        this.ParticleArray[i].mesh.scale.set(1,1,1);
      } 
    }
  }
}

export default JetStream



