<template>
  <div class="box">
      <!-- <play-state :process="state.process" :isReadyPlay="state.isReadyPlay" @confirm="handlePlay"/> -->
    	<div id="canvasFather"></div>
  </div>
</template>

<script>
// import PlayState from '../000-common/PlayState'

import LMThree from './THREEJS/LMThree'
import Robot from './Robot'
import SkyBox from './SkyBox'
import JetStream from './JetStream'
import DustStream from './DustStream'

let lmthree = {}
let robot = null
let skybox = null
let duststream = null
let jetsreams = []
let that = null
let flag = false
export default {
  props: {
    URL: {
      type: String,
      default: ''
    },
    playing: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      state: {
        process: 0,
        isReadyPlay: false
      }
    }
  },
  components: {
  },
  mounted() {
    that = this
     // 
     lmthree = new LMThree({canvasFatherId: 'canvasFather'})
     lmthree.scene.add(lmthree.lmcamera.debug)
     that.state = lmthree.lmaudio.state

     // 
     skybox = new SkyBox()
     lmthree.scene.add(skybox.mesh)

     // 
     robot = new Robot()
     robot.mesh.scale.set(0.5, 0.5, 0.5);
     robot.mesh.position.y = 0
     robot.mesh.rotation.z = Math.PI/20
     robot.mesh.rotation.x = Math.PI/10
     robot.mesh.name = 'robot'
     lmthree.scene.add(robot.mesh)
     lmthree.lmlight.shadowLight.target = robot.mesh

     // 
     jetsreams[0] = new JetStream()
     jetsreams[0].mesh.position.set(6,0,-30);
     jetsreams[0].mesh.rotation.x = Math.PI/6;	
     jetsreams[0].mesh.rotation.z = Math.PI/20;	
     lmthree.scene.add(jetsreams[0].mesh)

     jetsreams[1] = new JetStream()
     jetsreams[1].mesh.position.set(-14,0,-30);
     jetsreams[1].mesh.rotation.x = Math.PI/6;	
     jetsreams[1].mesh.rotation.z = Math.PI/20;	
     lmthree.scene.add(jetsreams[1].mesh)

     // 
     duststream = new DustStream()
     duststream.mesh.rotation.z = Math.PI/20;
     duststream.mesh.rotation.x = Math.PI/10;
     lmthree.scene.add(duststream.mesh)
     this.loop()
  },
  watch: {
    URL(val) {
      if(val) {
        // 
        lmthree.lmaudio.loadAudio(this.URL)
      }
    },
    playing(value) {
      if(value) {
        lmthree.lmaudio.play()
        this.controls.autoRotate = true;
      }
    }
  },
  methods: {
    loop() {
      if(lmthree.lmaudio.state.isReadyPlay && flag === false) {
        this.$emit('readyPlay',true)
        flag = true
      }
      if(lmthree.lmaudio.playing) {
        let numbers = lmthree.lmaudio.getAverageFrequency()
        duststream.speed(parseInt(numbers))
      }
      lmthree.lmrender.render.render(lmthree.scene, lmthree.lmcamera.camera)
      robot.blinkLoop();
      robot.idleAnimation();
      jetsreams[0].jet()
      jetsreams[1].jet()
      lmthree.controls.update();
      requestAnimationFrame(that.loop)
    },
    uploadFile(e) {
      let musicFile = e.target.files[0]
      lmthree.lmaudio.loadSong(musicFile)
      console.error(e.target.files[0])
    },
    handlePlay() {
     
    }
  }
}
</script>

<style lang="scss">
.box {
  width: 100%;
	height: 100%;

  #container {
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: hidden;
    background:#000000;
  }
}
</style>



