let THREE = window.THREE



class LMLight {
  constructor() {
    this.hemisphereLight = null;
    this.shadowLight = null;
    this.createLights()
  }

  createLights() {
    this.hemisphereLight = new THREE.HemisphereLight(0xffffff,0xffffff, 0.8)
    this.hemisphereLight.name = 'hemisphereLight'
    this.hemisphereLight.position.set(0, 0, 0);

    this.shadowLight = new THREE.DirectionalLight(0xffffff, 0.8);
    this.shadowLight.name = 'shadowLight'
    this.shadowLight.position.set(100, 100, 100);
    this.shadowLight.castShadow = true;
    this.shadowLight.shadow.camera.left = -400; // 
    this.shadowLight.shadow.camera.right = 400;
    this.shadowLight.shadow.camera.top = 400;
    this.shadowLight.shadow.camera.bottom = -400;
    this.shadowLight.shadow.camera.near = 1; // 
    this.shadowLight.shadow.camera.far = 1000; // 
  
    this.shadowLight.shadow.mapSize.width = 2048;
    this.shadowLight.shadow.mapSize.height = 2048;

    this.backLight = new THREE.DirectionalLight(0xffffff, 0.8)
    this.backLight.position.set(-100, 100, 100)
    this.backLight.castShadow = true
    this.backLight.shadowDarkness = 0.1
    this.backLight.shadowMapWidth =  1024
    this.backLight.name = 'backLight'
  }
}
export default LMLight



